oDAaiB,2BAA8B,CAAE,CAAA","file":"Bio.vue?vue&type=style&index=0&id=450c8b9e&lang=scss&scoped=true","sourcesContent":["\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"~@/styles/theme/colors\";\n@import \"~@/styles/theme/breaks\";\n.Bio {\n  &--intro {\n    flex: 0 50%;\n    padding-left: 2em;\n  }\n  &--position {\n    text-transform: capitalize;\n  }\n  &--role {\n    position: absolute;\n    display: inline-block;\n    white-space: nowrap;\n    font-size: 1.2rem;\n    font-weight: 100;\n    text-transform: uppercase;\n    transform: rotate(-90deg);\n    transform-origin: right bottom;\n    bottom: 100%;\n    right: 100%;\n    letter-spacing: .1em;\n    text-decoration: none;\n    color: inherit;\n    > .UiIcon {\n      opacity: 0 !important;\n      & {\n        vertical-align: -.68em;\n        margin-left: .25em;\n        opacity: .4;\n        cursor: pointer;\n      }\n    }\n    @include Break( min-width Breaks(3) ){\n      pointer-events: none;\n      >.UiIcon { display: none }\n    }\n    @include Break( max-width Breaks(3) ){\n      font-size: 1.1rem;\n      line-height: 1.8;\n    }\n  }\n  &--media {\n    width: 100%;\n    height: 32em;\n    object-fit: cover;\n    @include Break( (min-width Breaks(1)) (max-width Breaks(3)) ){\n        object-fit: contain;\n        object-position: center top;\n        height: auto;\n        max-height: 32em;\n        width: auto;\n    }\n    @include Break( max-width Breaks(1) ){\n      margin-right: -1rem;\n      width: 100%;\n      min-width: calc(100% + 2rem);\n    }\n  }\n  #content {\n    >.UiBox:only-child {\n      padding-top: 7.5rem;\n      padding-bottom: 10rem;\n    }\n    &:before,\n    &:after {\n      content: \"\";\n      position: absolute;\n      left: 50%;\n      width: 1px;\n      height: 8rem;\n      background: Color(theme);\n    }\n    &:before { top:   -1.5rem }\n    &:after  { bottom: 0      }\n  }\n  @include Break( max-width Breaks(3) ){\n    &--overview {\n      flex-flow: nowrap column;\n        @include Break( max-width Breaks(4) ){\n          padding-top: 6rem !important;\n        }\n      >:first-child {\n        margin-bottom: 1.5rem;\n        min-height: 300px;\n        @include Break( min-width Breaks(3) ){ min-width: 100% }\n        @include Break( max-width Breaks(1) ){ min-width: 100% }\n      }\n      >:last-child {\n        padding-left: 0 !important;\n        & > :not(p) { text-align: center !important }\n      }\n    }\n  }\n}\n","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-alt:   #7D1214; // or: #CB3E3D\n\n$colors:(\n  dark:    $c-black, // #0E0E0E\n  slate:   lighten($c-black,15%), // #323232,\n  silver:  lighten($c-black,67%), // #B8B8B8\n  cream:   lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  light:   mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:   $c-matte, // #F0F0F0\n  \n  theme:   $c-theme, // #BA9454\n  alt:     $c-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}"],"sourceRoot":""}]);

